<template>
  <div>
    <audio-player
      serverName="sr"
      :processCallback="callback"
    >
      <li>
        <p>Speaker Name：</p>
        <p style="color: #dd4814;">{{result}}</p>
      </li>
    </audio-player>
    <a-modal
      v-model="visible"
      title="还没有注册"
      on-ok="handleOk"
    >
      <template slot="footer">
        <a-button
          key="back"
          @click="handleCancel"
        >
          Return
        </a-button>
        <a-button
          key="submit"
          type="primary"
        >
          <router-link :to="{name:'注册声音'}" @click.native="fresh">
            注册
          </router-link>
        </a-button>
      </template>
      <p>你可以选择直接注册</p>
    </a-modal>
  </div>
</template>

<script>
import AudioPlayer from "../../components/audioPlayer/alter";

export default {
  components: {
    AudioPlayer,
  },
  data() {
    return {
      result: "",
      visible: false,
    };
  },
  methods: {
    callback(res) {
      this.result = res.result;
      if (this.result === "") {
        this.visible = true;
      }
    },
    fresh(){
      this.$router.go(0); 
    },
    handleCancel() {
      this.visible = false;
    }
  },
};
</script>