<template>
  <div>
    <a-select
      placeholder="请选择样本"
      style="width: 120px"
      @change="handleChange"
    >
      <template v-for="item in audioList">
        <a-select-option
          :key="item"
          :value="item"
        >{{item}}</a-select-option>
      </template>
    </a-select>
    <a-button
      :style="{ marginLeft: '24px' }"
      @click="play"
    >播放/暂停</a-button>
    <a-button
      :style="{ marginLeft: '24px' }"
      @click="stop"
    >停止</a-button>
    <a-button
      :style="{ marginLeft: '24px' }"
      @click="start_record"
    >开始录音</a-button>
    <a-button
      :style="{ marginLeft: '24px' }"
      @click="end_record"
    >停止录音</a-button>
    <input
      :style="{ marginLeft: '24px' }"
      name="file"
      type="file"
      @change="handleChangeFile"
    >
    <wavesurfer
      ref="surfer"
      :onready="on_ready"
      :onseek="seek"
      :audioprocess="audio_process"
    />
    <div
      id="presult3"
      class="tm-gallery-item presult"
      style="border: 1px solid #000;margin: auto;"
    >
      <ul style="font-size: xx-large;margin: 20px 80px;">
        <slot></slot>
      </ul>
    </div>
  </div>
</template>

<script>
import fileupload from "../../common/fileupload";
import post from "../../common/post";
import { form_post, text_post } from "../../common/otherpost";
import Recorder from "../../common/recorder";
import wavesurfer from "./wavesurfer";

export default {
  components: {
    wavesurfer,
  },
  props: ["serverName", "processCallback", "onplay", "processName"],
  data() {
    return {
      audioList: ["请选择样本"],
      dir: "",
      upload_dir: "",
      recorder: null,
      to_temp: false,
      mp3_name: "",
    };
  },
  methods: {
    handleChange(value) {
      if (value != "请选择样本") {
        this.to_temp = false;
        this.mp3_name = value;
        this.$refs.surfer.initAndLoadSpectrogram(this.getUrlServer(value));
      }
    },
    handleChangeFile(event) {
      var formData = new window.FormData();
      const file = event.target.files[0];
      formData.append("file", file);
      this.to_temp = true;
      this.mp3_name = file.name;
      fileupload("/upload/" + this.$props.serverName, formData, () => {
        this.$refs.surfer.initAndLoadSpectrogram(this.getUrlUpload(file.name));
      });
    },
    getUrlServer(name) {
      return this.getUrl(this.dir, name);
    },
    getUrlUpload(name) {
      return this.getUrl(this.upload_dir, name);
    },
    getUrl(dir, name) {
      return localStorage.getItem("apiurl") + dir + "/" + name;
    },
    record_stop_callback(blob) {
      var mp3_name = this.getTime() + ".webm";
      var formData = new window.FormData();
      formData.append("audio", blob); // form input type="file"
      formData.append("name", "asr");
      formData.append("mp3name", mp3_name);
      this.mp3_name = mp3_name;
      this.to_temp = true;
      form_post("/receive_audio", formData, () => {
        this.$refs.surfer.initAndLoadSpectrogram(this.getUrlUpload(mp3_name));
      });
    },
    play() {
      this.$refs.surfer.play();
    },
    stop() {
      this.$refs.surfer.stop();
    },
    start_record() {
      this.recorder.start_record();
    },
    end_record() {
      this.recorder.stop_record();
    },
    getTime() {
      var date = new Date();
      //年
      var year = date.getFullYear();
      //月
      var month = date.getMonth() + 1;
      //日
      var day = date.getDate();
      //时
      var hh = date.getHours();
      //分
      var mm = date.getMinutes();
      //秒
      var ss = date.getSeconds();
      var rq = year + month + day + hh + mm + ss;
      return rq;
    },
    audio_process(nowtime) {
      if (this.$props.onplay) {
        this.$props.onplay(nowtime);
      }
    },
    seek(nowtime) {
      if (this.$props.onplay) {
        this.$props.onplay(nowtime);
      }
    },
    on_ready() {
      var that = this;
      var serverName;
      if (this.$props.processName) serverName = this.$props.processName;
      else serverName = this.$props.serverName;
      if (that.to_temp && serverName != "meeting") {
        serverName += "/temp";
      }
      console.log(that.mp3_name)
      console.log(that.mp3_name.split('.')[0] + '.wav')
      text_post("/" + serverName, that.mp3_name.split('.')[0] + '.wav', (res) => {
        if (that.$props.processCallback) {
          that.$props.processCallback(res);
        }
      });
    },
  },
  mounted() {
    this.recorder = new Recorder(this.record_stop_callback);
    post("/getAudios", { serverName: this.$props.serverName }, (res) => {
      this.audioList = ["请选择样本"].concat(res.data);
      this.dir = res.dir;
      this.upload_dir = res.upload_dir;
    });
  },
};
</script>

<style scoped>
.presult li {
  text-decoration: none;
  list-style: none;
}
.presult li p {
  display: inline;
  background-color: #fcfcfc;
}

/* 定义keyframe动画，命名为blink */
@keyframes blink {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

/* 添加兼容性前缀 */
@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@-moz-keyframes blink {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@-ms-keyframes blink {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@-o-keyframes blink {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

/* 定义blink类*/
/* 定义blink类*/
.blink {
  color: #dd4814;
  animation: blink 0.5s linear infinite;
  /* 其它浏览器兼容性前缀 */
  -webkit-animation: blink 0.5s linear infinite;
  -moz-animation: blink 0.5s linear infinite;
  -ms-animation: blink 0.5s linear infinite;
  -o-animation: blink 0.5s linear infinite;
}
</style>