import axios from 'axios'

export function form_post(url, form, callback) {
  axios({
    url: localStorage.getItem('apiurl') + url,
    data: form,
    method: 'POST',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  }).then(result => {
    if (result.status == 200)
      callback(result.data);
    else
      console.log(result.statusText);

  }).catch(error => {
    console.log(error);
  })
}

export function text_post(url, text, callback) {
  axios({
    url: localStorage.getItem('apiurl') + url,
    data: text,
    method: 'POST'
  }).then(result => {
    if (result.status == 200)
      callback(result.data);
    else
      console.log(result.statusText);

  }).catch(error => {
    console.log(error);
  })
}