<template>
  <div>
    <audio-player
      serverName="sr"
      processName="meeting"
      :processCallback="callback"
    >
      <li>
        <p>Message：</p>
        <p>{{message}} </p> <br><br><br>
        <p>Speaker Name：</p>
        <p style="color: #dd4814;">{{speakername}}</p><br><br><br>
        <p>Speech Recognition </p>
        <p style="color: #dd4814;">{{text}}</p><br><br><br>
      </li>
    </audio-player>
  </div>
</template>

<script>
import AudioPlayer from "../../components/audioPlayer/alter";

export default {
  components: {
    AudioPlayer,
  },
  data() {
    return {
      speakername: "",
      text: "",
      message: "",
    };
  },
  methods: {
    callback(res) {
      this.speakername = res.sr.result;
      this.text = res.asr.result;
      var message = "successful recognition";
      if (this.speakername == "unknown") {
        message =
          "failure recognition， the voice is not registered in the system!";
        this.speakername = "";
        this.text = "";
      }
      this.message = message;
    },
  },
};
</script>