<template>
  <a-layout-sider
    width="20%"
    style="background: #fff"
  >
    <div class="logo">展示中心</div>
    <a-menu
      mode="inline"
      theme="dark"
      :defaultSelectedKeys="selectedKeys"
      :style="{ height: '100%', borderRight: 0 }"
    >
      <template v-for="item in menu">
        <a-menu-item :key="item.name">
          <router-link :to="{name:item.title}">{{item.title}}</router-link>
        </a-menu-item>
      </template>
    </a-menu>
  </a-layout-sider>
</template>

<script>
import Menu from "./Menu";

export default {
  data() {
    return {
      menu: [],
    };
  },
  mounted() {
    this.menu = Menu.getMenu();
  },
  computed: {
    selectedKeys() {
      return this.$route.path.split("/").slice(-1);
    },
  },
};
</script>

<style>
.logo {
  width: 100%;
  height: 64px;
  background: #001529;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 200%;
}
</style>