<template>
  <div>
    <a-button @click="start_record">录音</a-button>
    <a-button
      @click="end_record"
      :style="{ marginLeft: '12px' }"
    >停止录音</a-button>

    <div class="form-group">
      <label for="exampleFormControlTextarea1">Dialogs</label>
      <textarea
        class="form-control"
        rows="15"
        v-model="value"
      ></textarea>
    </div>
    <wavesurfer
      ref="surfer"
      :onready="on_ready"
    />
  </div>
</template>

<script>
import Recorder from "../../common/recorder";
import wavesurfer from "../../components/audioPlayer/wavesurfer";
import { form_post, text_post } from "../../common/otherpost";
export default {
  components: {
    wavesurfer,
  },
  data() {
    return {
      result: "",
      recorder: null,
      file: "",
      mp3name: "",
      value: "",
      timer: null,
      count: 0,
    };
  },
  methods: {
    callback(res) {
      this.result = res.result;
    },
    getTime() {
      var date = new Date();
      //年
      var year = date.getFullYear();
      //月
      var month = date.getMonth() + 1;
      //日
      var day = date.getDate();
      //时
      var hh = date.getHours();
      //分
      var mm = date.getMinutes();
      //秒
      var ss = date.getSeconds();
      var rq = year + month + day + hh + mm + ss;
      return rq;
    },
    start_record() {
      this.recorder.start_record();
      this.timer = setInterval(this.one_step_stop, 3000);
    },
    end_record() {
      /*this.recorder.stop_record();
      this.timer.close*/
      if (this.timer == null) {
        alert("please first beginning the recording before stopping! ");
      } else {
        clearInterval(this.timer);
        console.log("posting ended!");
      }
    },
    one_step_stop() {
      this.recorder.stop_record();
    },
    record_stop_callback(blob) {
      var mp3_name = this.getTime() + "_" + this.count + ".webm";
      this.count += 1;
      var formData = new window.FormData();
      formData.append("audio", blob); // form input type="file"
      formData.append("name", "asr");
      formData.append("mp3name", mp3_name);
      this.mp3_name = mp3_name;
      this.to_temp = true;
      form_post("/receive_audio", formData, () => {
        this.$refs.surfer.initAndLoadSpectrogram(
          this.getUrl("/static/uploads/temp", mp3_name)
        );
      });
      this.recorder.start_record();
    },
    getUrl(dir, name) {
      return localStorage.getItem("apiurl") + dir + "/" + name;
    },
    on_ready() {
      var that = this;
      text_post("/meeting", that.mp3_name.split('.')[0] + '.wav', (res) => {
        this.value = this.value + res.sr.result + ":" + res.asr.result + "\n";
      });
    },
  },
  mounted() {
    this.recorder = new Recorder(this.record_stop_callback);
  },
};
</script>

<style scoped>
.form-group {
  margin-bottom: 30px;
}

.form-control {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1.1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #626364;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-right: 0;
  border-top: 0;
  border-left: 0;
  border-radius: 0;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-control::-ms-expand {
  background-color: transparent;
  border: 0;
}

.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #80bdff;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.form-control::-webkit-input-placeholder {
  color: #6c757d;
  opacity: 1;
}

.form-control::-moz-placeholder {
  color: #6c757d;
  opacity: 1;
}

.form-control:-ms-input-placeholder {
  color: #6c757d;
  opacity: 1;
}

.form-control::-ms-input-placeholder {
  color: #6c757d;
  opacity: 1;
}

.form-control::placeholder {
  color: #6c757d;
  opacity: 1;
}
</style>