<template>
  <div class="waveformOuter">
    <div
      id="waveform"
      ref="waveform"
      style="border: 1px solid #000;"
    >
      <br />
      <div
        class="progress progress-striped active"
        id="progress-bar"
        ref="progressDiv"
      >
        <div
          class="progress-bar progress-bar-info"
          ref="progressBar"
        ></div>
      </div>
    </div>
    <div
      id="wave-timeline"
      ref="wavetimeline"
      style="background-color: azure"
    ></div>
    <p style="text-align: center;font-size: x-large; font-style: italic">waveform</p>
    <!-- Here be this.waveform -->
    <div
      id="wave-spectrogram"
      ref="wavespectrogram"
      style="border: 1px solid #000;"
    ></div>
    <p style="text-align: center;font-size: x-large; font-style: italic">spectrogram</p>
  </div>
</template>

<script>
import WaveSurfer from "wavesurfer.js";
import Timeline from "wavesurfer.js/dist/plugin/wavesurfer.timeline.js";
import Sectrogram from "wavesurfer.js/dist/plugin/wavesurfer.spectrogram.js";
export default {
  props: ["audioprocess", "onseek", "onready"],
  data() {
    return {
      wavesurfer: null
    };
  },
  methods: {
    initAndLoadSpectrogram(audioUrl) {
      var that = this;
      if (this.wavesurfer) {
        this.wavesurfer.destroy();
      }
      // 创建频谱图实例
      var options = {
        container: this.$refs.waveform,
        waveColor: "violet",
        progressColor: "#0000ff",
        loaderColor: "purple",
        cursorColor: "#ff0000",
        cursorWidth: 2,
        autoCenter: false,
        fillParent: true,
        hideScrollbar: false,
        plugins: [
          Timeline.create({
            //timeline plugin
            container: that.$refs.wavetimeline,
            height: 10,
          }),
          Sectrogram.create({
            container: that.$refs.wavespectrogram,
            labels: false,
          }),
        ],
      };

      this.wavesurfer = WaveSurfer.create(options);
      /* Progress bar */
      (function () {
        that.wavesurfer.on("loading", that.showProgress);
        that.wavesurfer.on("ready", that.hideProgress);
        that.wavesurfer.on("destroy", that.hideProgress);
        that.wavesurfer.on("error", that.hideProgress);
      })();

      // 前序操作已经保证：实时录音已上传到/temp目录下，而录音文件上传到其它目录下
      // 由于会议室记录系统不涉及语音文件上传，故先不考虑flag为false时的处理情况

      audioUrl = audioUrl.split(".")[0] + ".wav";
      this.wavesurfer.load(audioUrl); //网络地址

      this.wavesurfer.on("audioprocess", () => {
        var nowtime = that.wavesurfer.getCurrentTime().toFixed(2);
        if (that.$props.audioprocess) {
          that.$props.audioprocess(nowtime);
        }
      });
      this.wavesurfer.on("seek", () => {
        var nowtime = that.wavesurfer.getCurrentTime().toFixed(2);
        if (that.$props.onseek) {
          that.$props.onseek(nowtime);
        }
      });
      this.wavesurfer.on("ready", () => {
        var totaltime = that.wavesurfer.getDuration().toFixed(2);
        if (that.$props.onready) {
          that.$props.onready(totaltime);
        }
      });
    },
    showProgress(percent) {
      this.$refs.progressDiv.style.display = "block";
      this.$refs.progressBar.style.width = percent + "%";
    },
    hideProgress() {
      this.$refs.progressDiv.style.display = "none";
    },
    play() {
      this.wavesurfer.playPause();
    },
    stop() {
      this.wavesurfer.stop();
    },
  },
};
</script>