<template>
  <div>

    <a-tabs default-active-key="1">
      <a-tab-pane
        key="1"
        tab="简介"
      >
        雾在摄影时，是一种显著降低图像质量、影响观者视觉体验的干扰因素， 虽然在特定情况下，雾是艺术摄影中的重点关注对象， 但对于用户日常的拍摄以及地理成像等多种图像应用领域而言， 我们更希望能够减少雾的影响。因雾而降低质量的图像
      </a-tab-pane>
      <a-tab-pane
        key="2"
        tab="水下展示"
      >
        <video-player
          class="video-player vjs-custom-skin"
          ref="videoPlayer"
          :playsinline="true"
          :options="underOption"
        ></video-player>
      </a-tab-pane>
      <a-tab-pane
        key="3"
        tab="水下展示2"
      >
        <video-player
          class="video-player vjs-custom-skin"
          ref="videoPlayer"
          :playsinline="true"
          :options="under2Option"
        ></video-player>
      </a-tab-pane>
      <a-tab-pane
        key="4"
        tab="空中"
      >
        <video-player
          class="video-player vjs-custom-skin"
          ref="videoPlayer"
          :playsinline="true"
          :options="flyOption"
        ></video-player>
      </a-tab-pane>
      <a-tab-pane
        key="5"
        tab="实时去雾陆地"
      >
        <img
          class="video"
          :src="feedUrl"
          style="margin: 0 auto;"
        >
      </a-tab-pane>
      <a-tab-pane
        key="6"
        tab="实时去雾海上"
      >
        <img
          class="video"
          :src="feedUrlFog"
          style="margin: 0 auto;"
        >
      </a-tab-pane>
    </a-tabs>
  </div>
</template>

<script>
function makeOption(url) {
  return {
    playbackRates: [0.5, 1.0, 1.5, 2.0], //可选择的播放速度
    autoplay: false, //如果true,浏览器准备好时开始回放。
    muted: false, // 默认情况下将会消除任何音频。
    loop: false, // 视频一结束就重新开始。
    preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
    language: "zh-CN",
    aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
    fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
    sources: [
      {
        type: "",
        src: url, //url地址
      },
    ],
    poster: "", //你的封面地址
    // width: document.documentElement.clientWidth,
    notSupportedMessage: "此视频暂无法播放，请稍后再试", //允许覆盖Video.js无法播放媒体源时显示的默认信息。
    controlBar: {
      timeDivider: true, //当前时间和持续时间的分隔符
      durationDisplay: true, //显示持续时间
      remainingTimeDisplay: false, //是否显示剩余时间功能
      fullscreenToggle: true, //全屏按钮
    },
  };
}

export default {
  data() {
    return {
      result: "",
      underOption: makeOption(require("../../assets/Underwater.mp4")),
      under2Option: makeOption(require("../../assets/Whale.mp4")),
      flyOption: makeOption(require("../../assets/Plane.mp4")),
      feedUrl: localStorage.getItem("apiurl") + "/video_feed",
      feedUrlFog: localStorage.getItem("apiurl") + "/fog_video_feed",
    };
  },
};
</script>

<style scoped>
.video {
  width: 100%;
}
</style>