<template>
  <div>
    <a-form
      :form="form"
      :label-col="{ span: 5 }"
      :wrapper-col="{ span: 12 }"
      @submit="handleSubmit"
    >
      <a-form-item label="name">
        <a-input v-decorator="['name', { rules: [{ required: true, message: '请输入名字!' }] }]" />
      </a-form-item>
      <a-form-item label="提示">
        <label>声音信息(请录音或选择音频文件)</label>
      </a-form-item>
      <a-form-item label="上传文件">
        <a-upload :before-upload="beforeUpload">
          <a-button>
            <a-icon type="upload" /> 点击上传
          </a-button>
        </a-upload>
      </a-form-item>
      <a-form-item label="录音">
        <a-button @click="start_record">录音</a-button>
        <a-button
          @click="end_record"
          :style="{ marginLeft: '12px' }"
        >停止录音</a-button>
      </a-form-item>
      <div :style="{marginLeft:'16%' }">

      </div>

      <a-form-item :wrapper-col="{
        sm: { span: 16, offset: 5 },
      }">
        <a-button
          type="primary"
          @click="handleSubmit"
        >注册</a-button>
      </a-form-item>

    </a-form>
  </div>
</template>

<script>
import fileupload from "../../common/fileupload";
import Recorder from "../../common/recorder";
export default {
  data() {
    return {
      result: "",
      form: this.$form.createForm(this, { name: "coordinated" }),
      file: "",
      mp3name: "",
      recorder: null,
    };
  },
  methods: {
    callback(res) {
      this.result = res.result;
    },
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          if (this.filename != "") {
            var formData = new window.FormData();
            formData.append("audio", this.file); // form input type="file"
            formData.append("name", values.name);
            formData.append("mp3name", this.mp3name);
            fileupload("/enroll", formData, (res) => {
              this.$message.success(res.result);
              console.log(res);
            });
          } else {
            this.$message.error("文件不能为空");
          }
        }
      });
    },
    beforeUpload(file) {
      this.file = file;
      this.mp3name = file.name;
      return false;
    },
    getTime() {
      var date = new Date();
      //年
      var year = date.getFullYear();
      //月
      var month = date.getMonth() + 1;
      //日
      var day = date.getDate();
      //时
      var hh = date.getHours();
      //分
      var mm = date.getMinutes();
      //秒
      var ss = date.getSeconds();
      var rq = year + month + day + hh + mm + ss;
      return rq;
    },
    start_record() {
      this.recorder.start_record();
    },
    end_record() {
      this.recorder.stop_record();
    },
    record_stop_callback(blob) {
      var mp3_name = this.getTime() + ".webm";
      this.file = blob;
      this.mp3name = mp3_name;
    },
  },
  mounted() {
    this.recorder = new Recorder(this.record_stop_callback);
  },
};
</script>