import {
  Form,
  Icon,
  Input,
  Menu,
  Select,
  Layout,
  Button,
  Tooltip,
  PageHeader,
  Descriptions,
  List,
  Table,
  Alert,
  Result,
  Cascader,
  message,
  Modal,
  Upload,
  Tabs
} from 'ant-design-vue';
import Vue from 'vue';
import App from './App.vue';
import router from './router/routes';
import './layout/page.css';

Vue.use(Form);
Vue.use(Input);
Vue.use(Icon);
Vue.use(Select);
Vue.use(Menu);
Vue.use(Layout);
Vue.use(Button);
Vue.use(Tooltip);
Vue.use(PageHeader);
Vue.use(Descriptions);
Vue.use(List);
Vue.use(Table);
Vue.use(Alert);
Vue.use(Result);
Vue.use(Cascader);
Vue.use(Modal)
Vue.use(Upload)
Vue.use(Tabs)

Vue.prototype.$message = message;

import VideoPlayer from 'vue-video-player'
require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')

Vue.use(VideoPlayer)

message.config({
  duration: 2
});

/*import data from '../public/config.json'
localStorage.setItem('apiurl', data.apiurl)*/

if (process.env.NODE_ENV === 'development') {
  localStorage.setItem('apiurl', '/api')
} else {
  localStorage.setItem('apiurl', '')
}



new Vue({
  el: '#app',
  router,
  render: h => h(App)
})