import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../layout/MainLayout.vue'

import Home from '../pages/main/Home'

import ASR from '../pages/asr/index'
import ER from '../pages/er/index'
import SED from '../pages/sed/index'
import VR from '../pages/vr/index'
import PSR from '../pages/psr/index'
import Enroll from '../pages/enroll/index'
import Video from '../pages/video/index'
import Meeting from '../pages/meeting/index'

Vue.use(VueRouter);

const router = new VueRouter({
  base: __dirname,
  linkActiveClass: "active",
  routes: [{
    name: 'Main',
    path: '/',
    redirect: '/home',
    component: Main,
    children: [{
      path: '/home',
      name: '主页',
      component: Home
    }, {
      path: '/asr',
      name: '自动语音识别',
      component: ASR
    }, {
      path: '/er',
      name: '情感识别',
      component: ER
    }, {
      path: '/sed',
      name: '声音事件检测',
      component: SED
    }, {
      path: '/vr',
      name: '声纹识别',
      component: VR
    }, {
      path: '/psr',
      name: '个性化语音识别',
      component: PSR
    }, {
      path: '/enroll',
      name: '注册声音',
      component: Enroll
    }, {
      path: '/video',
      name: '去雾实验',
      component: Video
    }, {
      path: '/meeting',
      name: '会议室记录系统',
      component: Meeting
    }]
  }]
})

export default router;