import axios from 'axios'

export default function fileupload(url, form, onSuccess) {
  axios
    .post(localStorage.getItem('apiurl') + url, form, {
      method: 'POST',
      headers: {
        "Content-Type": "multipart/form-data;charset=utf-8",
      },
      withCredentials: true
    })
    .then((result) => {
      if (result.status == 200) onSuccess(result.data);
      else console.log(result.statusText);
    })
    .catch((error) => {
      console.log(error);
    });
}