var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('h2',[_vm._v("实验室简介")]),_c('p',[_vm._v("大数据与智能认知实验室隶属于武汉光电国家研究中心信息存储与光显示功能实验室。现拥有教授1人，副教授1人，在读全日制博士、硕士研究生近30人。")]),_c('p',[_vm._v("陈进才教授2000年毕业于西安交通大学计算机系统结构专业，获博士学位，师从郑守淇教授。2001 年1 月至2003 年1 月在华中科技大学外存储系统国家专业实验室从事博士后研究工作，师从张江陵教授。2011 年10 月至2012年10 月由国家公派到美国加州大学圣克鲁斯分校(University of California, Santa Cruz)存储系统研究中心(Storage Systems Research Center，SSRC)访学，与研究中心主任Darrell D.E. Long 教授和Ethan L. Miller 教授做合作研究。近年主持国家自然科学基金项目3项，参加国家“973”计划项目1项、国家“863”计划项目4项，发表学术论文80余篇，获授权国家发明专利30余项、国际发明专利2项。")]),_c('p',[_vm._v("卢萍副教授1988年本科毕业于华中理工大学（现华中科技大学），1993年获华中理工大学计算机系统结构硕士学位。主要从事信息存储理论与技术，大数据处理与机器学习，图像处理与情感识别方面的科学研究工作，完成了国家科技攻关项目，国家自然科学重点基金课题，国家“863”计划等国家重大项目的研究以及GF横向合作项目，申请国家发明专利多项。承担计算机专业本科校级精品课程《C语言程序设计》、省级精品课程《微机接口技术》、《verilog语言》的教学工作，多次获校教学质量优秀奖，正式出版教材2部，获校教学成果一等奖1次，获湖北省多媒体课件二等奖1次，获“国信蓝点杯”全国软件专业人才设计与开发大赛全国总决赛C语言程序设计本科组一等奖优秀指导教师奖2次。")]),_c('h2',[_vm._v("主要研究方向")]),_c('p',[_vm._v("实验室学术方向宽厚，具有创新、活跃、自由开放的学术氛围，实验室的主要学术方向及研究内容包括：存储系统及技术、语音识别、声纹识别、多模态情感计算、视觉问答技术、声音事件检测、视频异常事件检测、图像超分辨率重建。")]),_c('h2',[_vm._v("主要研究成果")]),_c('ol',[_c('li',[_c('p',[_vm._v("本研究团队在2020年7月的《IEEE Transactions on Magnetics》 上发表了题为 “High Field Enhancement of Plasmonics Antenna Using Ring Resonator for HAMR”的封面文章（doi: 10.1109/TMAG.2020.2990525; https://ieeexplore.ieee.org/document/9082031；该文的第一作者博士生陈玮，通讯作者陈进才教授）。该研究利用微环波导谐振器，将输入光场的能量集中在微环内来提高光场强度，微环内增强的光场可提高HAMR光组件中近场转换器(Near-field transducer，NFT)的表面等离子体基元耦合效率, 进而成倍提高磁记录单元的光吸收效率。相比于其他光结构，微环结构可以将光能量约束在微环内，在无需提高激光功率的前提下，循环利用光场来激发NFT的表面等离子体基元，有效解决了光热吸收效率低的问题，同时降低了激光功率成本。此外，课题组针对超高密度二维磁存储的关键技术进行了系统研究，提出了多磁头阵列的信号联合检测算法、基于神经网络的编解码算法、写磁头优化设计方法等，为实现超高密度磁记录提供理论和技术支持，多项相关成果已经在领域内高水平期刊及国际会议上发表。")]),_c('strong',[_vm._v("W. Chen, J. Chen, Z. Gan, K. Luo, Z. Huang and P. Lu, \"High-Field Enhancement of Plasmonics Antenna Using Ring Resonator for HAMR,\" in IEEE Transactions on Magnetics, vol. 56, no. 7, pp. 1-5, July 2020, Art no. 3000105, doi: 10.1109/TMAG.2020.299052