<template>
  <div>
    <audio-player
      serverName="ser"
      :processCallback="callback"
    >
      <li>
        <p>Emotion Analysis: </p>
        <p style="color: #dd4814;">{{result}}</p>
      </li>
    </audio-player>
  </div>
</template>

<script>
import AudioPlayer from "../../components/audioPlayer";

export default {
  components: {
    AudioPlayer,
  },
  data() {
    return {
      result: "",
    };
  },
  methods: {
    callback(res) {
      this.result = res.result;
    },
  },
};
</script>